/**
 * How to apply for postgraduate taught degrees
 *
 * Don't run this via patterns.js, it's a separate compilation.
 *
 * @module patterns/how-to-apply/how-to-apply--pgt
 * @author Web Development
 * @copyright City, University of London 2020
 */

import React, { useEffect, useState } from 'react';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';

import ApplyLinks from './buttons/apply';
import PropTypes from 'prop-types';
import createFocusTrap from 'focus-trap';

/**
 * Launch the how to apply modal.
 *
 * @param {object} props The JSON configuration file for the modal.
 * @return {object} The React component to render.
 */
function HowToApply(props) {
    let entryPoints = props.config;
    const [modalVisible, setModalVisible] = useState(false),
        [firstStep, setFirstStep] = useState(false), // Controls 'Start again' visibility
        [progressQualification, setProgressQualification] = useState(),
        [progressSubject, setProgressSubject] = useState(),
        [progressDate, setProgressDate] = useState(),
        [progressMethod, setProgressMethod] = useState(),
        [
            furtherStepsPendingIndicator,
            setFurtherStepsPendingIndicator,
        ] = useState(),
        [windowPrompt, setWindowPrompt] = useState(
            'Choose the qualification you wish to apply for:'
        ),
        promptQualification = 'Choose the qualification you wish to apply for:',
        promptRoute = 'Choose the route you wish to apply for:',
        promptEntryPoint = 'Choose the entry point you wish to apply for:',
        promptMethod = 'Apply online now:',
        furtherStepsPending = (
            <li className="how-to-apply--pgt--js__modal__progress__next">
                <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                    <span className="how-to-apply--pgt--js__modal__progress__circle">
                        <span
                            className="fas fa-circle icon"
                            aria-hidden="true"
                        ></span>
                    </span>
                </span>
            </li>
        );

    let [btnSelection, setBtnSelection] = useState(),
        [dateSelection, setDateSelection] = useState(),
        [linkSelection, setLinkSelection] = useState(),
        [methodSelection, setMethodSelection] = useState(),
        [locationOneHeading, setLocationOneHeading] = useState(),
        [
            locationOneDateSelectionButton,
            setLocationOneDateSelectionButton,
        ] = useState(),
        [
            locationOneDateSelectionLink,
            setLocationOneDateSelectionLink,
        ] = useState(),
        [locationTwoHeading, setLocationTwoHeading] = useState(),
        [
            locationTwoDateSelectionButton,
            setLocationTwoDateSelectionButton,
        ] = useState(),
        [
            locationTwoDateSelectionLink,
            setLocationTwoDateSelectionLink,
        ] = useState(),
        selectedQualificationData,
        selectedQualificationValue,
        selectedSubjectData,
        selectedSubjectValue,
        selectedDateData,
        selectedDateValue;

    /**
     * Convert raw date values to full month/year for rendering to buttons.
     *
     * @param {string} sourceDate The unformatted date string.
     */
    function formatDate(sourceDate) {
        const months = [
            'January',
            'February',
            'March',
            'April',
            'May',
            'June',
            'July',
            'August',
            'September',
            'October',
            'November',
            'December',
        ];
        let formattedDate = new Date(sourceDate);
        return `${
            months[formattedDate.getUTCMonth()]
        } ${formattedDate.getUTCFullYear()}`;
    }

    /**
     * Completed programme, subject and dates filter. Analyse methods data.
     *
     * @param {object} data The filtered data record data, where methods are at the top level.
     */
    function filterMethodsData(methods) {
        // Methods of study pre-selection prompt text
        setWindowPrompt(promptMethod);

        // Empty relevant state variables to remove superfluous button/link rendering
        setMethodSelection();
        setDateSelection();
        setBtnSelection();
        setLinkSelection();

        // Variables scoped to this function
        let qualNav, subjNav, dateNav, methodNav;

        selectedQualificationData
            ? (qualNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              // Load qualification select; clear subject, date and method from progress navigation
                              filterQualificationData(entryPoints);
                              setProgressSubject();
                              setProgressDate();
                              setProgressMethod();
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose qualification"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              {selectedQualificationValue}
                          </span>
                      </button>
                  </li>
              ))
            : (qualNav = null);
        setProgressQualification(qualNav);

        selectedSubjectData
            ? (subjNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              // Load subject select; clear date and method from progress navigation
                              setProgressDate();
                              setProgressMethod();
                              if (selectedQualificationData) {
                                  filterSubjectData(
                                      selectedQualificationData[0]['options']
                                  );
                              } else {
                                  filterQualificationData(entryPoints);
                              }
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose subject"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              {selectedSubjectValue}
                          </span>
                      </button>
                  </li>
              ))
            : (subjNav = null);
        setProgressSubject(subjNav);

        selectedDateData
            ? (dateNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              // Load date select; clear method from progress navigation
                              setProgressMethod();
                              if (selectedSubjectData) {
                                  filterDatesData(
                                      selectedSubjectData[0]['options']
                                  );
                              } else if (selectedQualificationData) {
                                  filterSubjectData(
                                      selectedQualificationData[0]['options']
                                  );
                              } else {
                                  filterQualificationData(entryPoints);
                              }
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose subject"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              {selectedDateValue}
                          </span>
                      </button>
                  </li>
              ))
            : (dateNav = null);
        setProgressDate(dateNav);

        // Method loads apply links and, if present, will always be the last step, therefore progress icon does nothing on click
        methodNav = (
            <li className="how-to-apply--pgt--js__modal__progress__current">
                <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                    <span className="how-to-apply--pgt--js__modal__progress__circle">
                        <span className="fas fa-circle icon"></span>
                    </span>
                    <span className="how-to-apply--pgt--js__modal__progress__text">
                        Method of study
                    </span>
                </span>
            </li>
        );
        setProgressMethod(methodNav);

        // Final modal step; remove further steps pending indicator
        setFurtherStepsPendingIndicator();

        let methodLinks = [];
        let linkOptions;

        methods.map((m) => {
            const methodLink = {
                text: m.header,
                link: m.options.apply,
            };
            methodLinks.push(methodLink);
            linkOptions = <ApplyLinks data={methodLinks} />;
            setMethodSelection(linkOptions);
        });
    }

    /**
     * Completed programme and subject filter. Analyse dates data.
     *
     * @param {object} data The filtered data record data, where dates are at the top level.
     */
    function filterDatesData(data) {
        // Date pre-selection prompt text
        setWindowPrompt(promptEntryPoint);

        // Empty relevant state variables to remove superfluous button/link rendering
        setMethodSelection();
        setBtnSelection();
        setLinkSelection();

        // Empty relevant state variables to remove superfluous progress navigation rendering
        setProgressMethod();

        // Clear data stored from previous selections
        selectedDateData = null;

        // Variables scoped to this function
        let buttonOptions,
            linkOptions,
            dateButtons = [],
            dateLinks = [],
            locationOneDateLinks = [],
            locationTwoDateLinks = [],
            locationOptions = [],
            locationOneDateAsButton = false,
            locationOneDateAsLink = false,
            locationTwoDateAsButton = false,
            locationTwoDateAsLink = false,
            qualNav,
            subjNav,
            dateNav,
            dateLink;

        // Progress indicator; display selected qualification and subject values
        selectedQualificationData
            ? (qualNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              filterQualificationData(entryPoints);
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose qualification"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              {selectedQualificationValue}
                          </span>
                      </button>
                  </li>
              ))
            : (qualNav = null);
        setProgressQualification(qualNav);

        selectedSubjectData
            ? (subjNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              filterSubjectData(
                                  selectedQualificationData[0]['options']
                              );
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose subject"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              {selectedSubjectValue}
                          </span>
                      </button>
                  </li>
              ))
            : (subjNav = null);
        setProgressSubject(subjNav);

        // Method loads apply links and, if present, will always be the last step, therefore progress icon does nothing on click
        if (selectedDateData) {
            dateNav = (
                <li className="how-to-apply--pgt--js__modal__progress__current">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            Entry point
                        </span>
                    </span>
                </li>
            );
        } else {
            dateNav = null;
        }

        setProgressDate(dateNav);

        data.map((d) => {
            let locations = d['options'];
            locations.map((l) => {
                let methods = l['options'];
                methods.map(() => {
                    // Multiple dates; print buttons
                    if (data.length > 1) {
                        // Date selection required: set progress indicator to subject and remove methods
                        selectedDateValue = (
                            <li className="how-to-apply--pgt--js__modal__progress__current">
                                <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                                    <span className="how-to-apply--pgt--js__modal__progress__circle">
                                        <span className="fas fa-circle icon"></span>
                                    </span>
                                    <span className="how-to-apply--pgt--js__modal__progress__text">
                                        Entry point
                                    </span>
                                </span>
                            </li>
                        );
                        setProgressDate(selectedDateValue);
                        setProgressMethod();

                        // Calculate number of locations
                        locations.map((lo) => {
                            locationOptions.push(lo.header);
                        });
                        locationOptions = locationOptions.reduce(function (
                            a,
                            i
                        ) {
                            a.indexOf(i) === -1 ? a.push(i) : null;
                            return a;
                        },
                        []);

                        // Multiple locations, multiple dates => create array of objects per location
                        if (locationOptions.length > 1) {
                            let locationsAll = [];
                            locationOptions.map((lo) => {
                                let filteredData = data.filter(
                                    (fd) => fd['options'][0]['header'] === lo
                                );
                                locationsAll.push(filteredData);
                            });

                            // Location 1 output
                            locationsAll[0].map((fd) => {
                                let methods = fd['options'][0]['options'];
                                if (methods.length > 1) {
                                    // Dates as buttons
                                    return (
                                        <li key={fd}>
                                            <button>
                                                <span>{fd.header}</span>
                                            </button>
                                        </li>
                                    );
                                } else {
                                    // Dates as links
                                    const dateLink = {
                                        text: formatDate(fd.header),
                                        link:
                                            fd['options'][0]['options'][0][
                                                'options'
                                            ]['apply'],
                                    };
                                    locationOneDateLinks.push(dateLink);
                                    locationOneDateLinks = Array.from(
                                        new Set(
                                            locationOneDateLinks.map(
                                                (a) => a.text
                                            )
                                        )
                                    ).map((text) => {
                                        return locationOneDateLinks.find(
                                            (a) => a.text === text
                                        );
                                    });
                                    locationOneDateAsLink = (
                                        <ApplyLinks
                                            data={locationOneDateLinks}
                                        />
                                    );
                                }
                            });

                            // Location 2 output
                            locationsAll[1].map((fd) => {
                                let methods = fd['options'][0]['options'];
                                if (methods.length > 1) {
                                    // Dates as buttons
                                    return (
                                        <li key={fd}>
                                            <button>
                                                <span>{fd.header}</span>
                                            </button>
                                        </li>
                                    );
                                } else {
                                    // Dates as links
                                    const dateLink = {
                                        text: formatDate(fd.header),
                                        link:
                                            fd['options'][0]['options'][0][
                                                'options'
                                            ]['apply'],
                                    };
                                    locationTwoDateLinks.push(dateLink);
                                    locationTwoDateLinks = Array.from(
                                        new Set(
                                            locationTwoDateLinks.map(
                                                (a) => a.text
                                            )
                                        )
                                    ).map((text) => {
                                        return locationTwoDateLinks.find(
                                            (a) => a.text === text
                                        );
                                    });
                                    locationTwoDateAsLink = (
                                        <ApplyLinks
                                            data={locationTwoDateLinks}
                                        />
                                    );
                                }
                            });

                            setLocationOneHeading(
                                <h3>
                                    {locationsAll[0][0]['options'][0]['header']}
                                </h3>
                            );
                            setLocationOneDateSelectionButton(
                                locationOneDateAsButton
                            );
                            setLocationOneDateSelectionLink(
                                locationOneDateAsLink
                            );
                            setLocationTwoHeading(
                                <h3>
                                    {locationsAll[1][0]['options'][0]['header']}
                                </h3>
                            );
                            setLocationTwoDateSelectionButton(
                                locationTwoDateAsButton
                            );
                            setLocationTwoDateSelectionLink(
                                locationTwoDateAsLink
                            );

                            // End multiple locations
                        } else {
                            // Single location, multiple dates
                            if (locations.length > 1 || methods.length > 1) {
                                // Single location, multiple dates, multiple methods => Dates as standard button; further options to follow
                                dateButtons.push(d.header);
                                dateButtons = dateButtons.reduce(function (
                                    a,
                                    i
                                ) {
                                    a.indexOf(i) === -1 ? a.push(i) : null;
                                    return a;
                                },
                                []);
                                buttonOptions = dateButtons.map((db) => {
                                    return (
                                        <li key={db}>
                                            <button>
                                                <span
                                                    data-date-src={db}
                                                    onClick={(e) => {
                                                        let dateSrc = e.target.getAttribute(
                                                            'data-date-src'
                                                        );
                                                        selectedDateValue = formatDate(
                                                            dateSrc
                                                        );
                                                        selectedDateData = data.filter(
                                                            (fd) =>
                                                                fd.header ===
                                                                dateSrc
                                                        );
                                                        filterMethodsData(
                                                            selectedDateData[0][
                                                                'options'
                                                            ][0]['options']
                                                        );
                                                        setFirstStep(false);
                                                    }}
                                                >
                                                    {formatDate(db)}
                                                </span>
                                            </button>
                                        </li>
                                    );
                                });

                                // Modal journey incomplete; display further steps pending indicator
                                setFurtherStepsPendingIndicator(
                                    furtherStepsPending
                                );
                            } else {
                                // Single location, multiple dates, single method => Dates as links
                                dateLinks.push(d.header);
                                dateLinks = dateLinks.reduce(function (a, i) {
                                    a.indexOf(i) === -1 ? a.push(i) : null;
                                    return a;
                                }, []);

                                // Map each unique date and filter data to extract correct apply links
                                const dateLinksData = [];
                                dateLinks.map((lq) => {
                                    let filteredDateData = data.filter(
                                        (q) => q.header === lq
                                    );
                                    dateLink = {
                                        text: formatDate(lq),
                                        link:
                                            filteredDateData[0]['options'][0][
                                                'options'
                                            ][0]['options']['apply'],
                                    };
                                    dateLinksData.push(dateLink);
                                });
                                linkOptions = (
                                    <ApplyLinks data={dateLinksData} />
                                );
                                // Final modal step; remove further steps indicator
                                setFurtherStepsPendingIndicator();
                            }
                        }
                    } else {
                        // Single date; move to methods function
                        filterMethodsData(data[0]['options'][0]['options']);
                    }
                });
            });
        });

        linkOptions ? setDateSelection(linkOptions) : null;
        buttonOptions ? setDateSelection(buttonOptions) : null;
    }

    /**
     * Completed programme filter. Analyse subjects data.
     *
     * @param {object} data The filtered data record data, where subjects are at the top level.
     */
    function filterSubjectData(data) {
        // Empty relevant state variables to remove superfluous button/link rendering
        setDateSelection();
        setMethodSelection();
        setBtnSelection();
        setLinkSelection();

        // Empty relevant state variables to remove superfluous progress navigation rendering
        setProgressDate();
        setProgressMethod();

        // Clear data stored from previous selections
        selectedSubjectData = null;
        selectedDateData = null;

        // Variables scoped to this function
        let buttonQuals = [],
            linkRoutes = [],
            routeLink,
            buttonOptions,
            linkOptions,
            locationOptions = [],
            qualNav;

        // Progress indicator; display selected qualification value
        selectedQualificationData
            ? (qualNav = (
                  <li className="how-to-apply--pgt--js__modal__progress__previous">
                      <button
                          className="how-to-apply--pgt--js__modal__progress__wrapper"
                          onClick={() => {
                              filterQualificationData(entryPoints);
                          }}
                      >
                          <span className="how-to-apply--pgt--js__modal__progress__circle">
                              <span
                                  className="fas fa-check icon"
                                  aria-label="Back to choose qualification"
                              ></span>
                          </span>
                          <span className="how-to-apply--pgt--js__modal__progress__text">
                              {selectedQualificationValue}
                          </span>
                      </button>
                  </li>
              ))
            : (qualNav = null);
        setProgressQualification(qualNav);

        data.map((s) => {
            // Subjects pre-selection prompt text
            setWindowPrompt(promptRoute);
            let dates = s['options'];
            dates.map((d) => {
                let locations = d['options'];
                locations.map((l) => {
                    let methods = l['options'];
                    methods.map(() => {
                        // Store each unique location value in an array
                        locations.map((lo) => {
                            locationOptions.push(lo.header);
                        });
                        locationOptions = locationOptions.reduce(function (
                            a,
                            i
                        ) {
                            a.indexOf(i) === -1 ? a.push(i) : null;
                            return a;
                        },
                        []);
                        if (data.length > 1) {
                            // Subject selection required: set progress indicator to subject and remove dates/methods
                            selectedSubjectValue = (
                                <li className="how-to-apply--pgt--js__modal__progress__current">
                                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                                            <span className="fas fa-circle icon"></span>
                                        </span>
                                        <span className="how-to-apply--pgt--js__modal__progress__text">
                                            Route
                                        </span>
                                    </span>
                                </li>
                            );
                            setProgressSubject(selectedSubjectValue);
                            setProgressDate();
                            setProgressMethod();

                            if (
                                dates.length > 1 ||
                                locationOptions.length > 1 ||
                                methods.length > 1
                            ) {
                                // Standard subject button
                                buttonQuals.push(s.header);
                                buttonQuals = buttonQuals.reduce(function (
                                    a,
                                    i
                                ) {
                                    a.indexOf(i) === -1 ? a.push(i) : null;
                                    return a;
                                },
                                []);
                                buttonOptions = buttonQuals.map((bq) => {
                                    return (
                                        <li key={bq}>
                                            <button>
                                                <span
                                                    onClick={(e) => {
                                                        selectedSubjectValue =
                                                            e.target
                                                                .textContent;
                                                        selectedSubjectData = data.filter(
                                                            (subj) =>
                                                                subj.header ===
                                                                selectedSubjectValue
                                                        );
                                                        filterDatesData(
                                                            selectedSubjectData[0][
                                                                'options'
                                                            ]
                                                        );
                                                        setFirstStep(false);
                                                    }}
                                                >
                                                    {bq}
                                                </span>
                                            </button>
                                        </li>
                                    );
                                });
                                setBtnSelection(buttonOptions);
                                // Modal journey incomplete; display further steps pending indicator
                                setFurtherStepsPendingIndicator(
                                    furtherStepsPending
                                );
                            } else {
                                // Route as link
                                linkRoutes.push(s.header);
                                linkRoutes = linkRoutes.reduce(function (a, i) {
                                    a.indexOf(i) === -1 ? a.push(i) : null;
                                    return a;
                                }, []);

                                // Map each unique route and filter data to extract correct apply links
                                const routeLinksData = [];
                                linkRoutes.map((lr) => {
                                    let filteredRouteData = data.filter(
                                        (q) => q.header === lr
                                    );
                                    routeLink = {
                                        text: lr,
                                        link:
                                            filteredRouteData[0]['options'][0][
                                                'options'
                                            ][0]['options'][0]['options'][
                                                'apply'
                                            ],
                                    };
                                    routeLinksData.push(routeLink);
                                });
                                linkOptions = (
                                    <ApplyLinks data={routeLinksData} />
                                );
                                // Final modal step; remove further steps indicator
                                setFurtherStepsPendingIndicator();
                            }
                        } else {
                            // Move to dates function
                            filterDatesData(data[0]['options']);
                        }
                    });
                });
            });
        });

        setBtnSelection(buttonOptions);
        setLinkSelection(linkOptions);
    }

    /**
     * Analyse programmes data.
     *
     * @param {object} data The original data record data, where programmes are at the top level.
     */
    function filterQualificationData(data) {
        // If qualification select exists, it will be the first step in the modal; disable 'Start again' option
        setFirstStep(true);

        // Qualification pre-selection prompt text
        setWindowPrompt(promptQualification);

        // Empty relevant state variables to remove superfluous button/link rendering
        // setSubjectSelection();
        setDateSelection();
        setMethodSelection();
        setBtnSelection();
        setLinkSelection();

        // Empty relevant state variables to remove superfluous progress navigation rendering
        setProgressSubject();
        setProgressDate();
        setProgressMethod();

        // Clear data stored from previous selections
        selectedQualificationData = null;
        selectedSubjectData = null;
        selectedDateData = null;

        // Variables scoped to this function
        let buttonQuals = [],
            linkQuals = [],
            buttonOptions,
            linkOptions,
            locationOptions = [],
            qualNav;

        if (data.length > 1) {
            // Qualification selection required: set progress indicator to qualification and remove subjects/dates/methods
            selectedQualificationValue = (
                <li className="how-to-apply--pgt--js__modal__progress__current">
                    <span className="how-to-apply--pgt--js__modal__progress__wrapper">
                        <span className="how-to-apply--pgt--js__modal__progress__circle">
                            <span className="fas fa-circle icon"></span>
                        </span>
                        <span className="how-to-apply--pgt--js__modal__progress__text">
                            Qualification
                        </span>
                    </span>
                </li>
            );
            setProgressQualification(selectedQualificationValue);
        }

        data.length > 1
            ? data.map((e) => {
                  let subjectNames = e['options'];
                  subjectNames.map((s) => {
                      let dates = s['options'];
                      dates.map((d) => {
                          let locations = d['options'];
                          locations.map((l) => {
                              let methods = l['options'];
                              methods.map(() => {
                                  // Store each unique location value in an array
                                  locations.map((lo) => {
                                      locationOptions.push(lo.header);
                                  });
                                  locationOptions = locationOptions.reduce(
                                      function (a, i) {
                                          a.indexOf(i) === -1
                                              ? a.push(i)
                                              : null;
                                          return a;
                                      },
                                      []
                                  );
                                  if (data.length > 1) {
                                      if (
                                          subjectNames.length > 1 ||
                                          dates.length > 1 ||
                                          locationOptions.length > 1 ||
                                          methods.length > 1
                                      ) {
                                          buttonQuals.push(e.header);
                                          buttonQuals = buttonQuals.reduce(
                                              function (a, i) {
                                                  a.indexOf(i) === -1
                                                      ? a.push(i)
                                                      : null;
                                                  return a;
                                              },
                                              []
                                          );
                                          buttonOptions = buttonQuals.map(
                                              (bq) => {
                                                  return (
                                                      <li key={bq}>
                                                          <button>
                                                              <span
                                                                  onClick={(
                                                                      e
                                                                  ) => {
                                                                      selectedQualificationValue =
                                                                          e
                                                                              .target
                                                                              .innerText;
                                                                      selectedQualificationData = data.filter(
                                                                          (
                                                                              qual
                                                                          ) =>
                                                                              qual.header ===
                                                                              selectedQualificationValue
                                                                      );
                                                                      filterSubjectData(
                                                                          selectedQualificationData[0][
                                                                              'options'
                                                                          ]
                                                                      );
                                                                      setFirstStep(
                                                                          false
                                                                      );
                                                                  }}
                                                              >
                                                                  {bq}
                                                              </span>
                                                          </button>
                                                      </li>
                                                  );
                                              }
                                          );
                                          setBtnSelection(buttonOptions);
                                          // Modal journey incomplete; display further steps pending indicator
                                          setFurtherStepsPendingIndicator(
                                              furtherStepsPending
                                          );
                                      } else {
                                          linkQuals.push(e.header);
                                          linkQuals = linkQuals.reduce(
                                              function (a, i) {
                                                  a.indexOf(i) === -1
                                                      ? a.push(i)
                                                      : null;
                                                  return a;
                                              },
                                              []
                                          );

                                          // Map each unique qualification and filter data to extract correct apply links
                                          const methodLinks = [];
                                          linkQuals.map((lq) => {
                                              let filteredQualData = data.filter(
                                                  (q) => q.header === lq
                                              );
                                              const methodLink = {
                                                  text: lq,
                                                  link:
                                                      filteredQualData[0][
                                                          'options'
                                                      ][0]['options'][0][
                                                          'options'
                                                      ][0]['options'][0][
                                                          'options'
                                                      ]['apply'],
                                              };
                                              methodLinks.push(methodLink);
                                          });
                                          linkOptions = (
                                              <ApplyLinks data={methodLinks} />
                                          );
                                          setLinkSelection(linkOptions);
                                      }
                                  }
                              });
                          });
                      });
                  });
              })
            : // One qualification; move to subjects function
              filterSubjectData(entryPoints[0]['options'], qualNav);
    }

    // Modal wrapper render
    const question = 'qualification',
        qualificationQuestion = question === 'qualification' && (
            <>
                <div className="how-to-apply--pgt--js__modal__content-wrapper">
                    <p>{windowPrompt}</p>
                    {/* Different JSX needed when multiple locations present */}
                    {!(
                        locationOneHeading ||
                        locationOneDateSelectionButton ||
                        locationOneDateSelectionLink ||
                        locationTwoHeading ||
                        locationTwoDateSelectionButton ||
                        locationTwoDateSelectionLink
                    ) ? (
                        <ul className="how-to-apply--pgt--js__options">
                            {dateSelection}
                            {methodSelection}
                            {btnSelection}
                            {linkSelection}
                        </ul>
                    ) : (
                        <React.Fragment>
                            <h3>{locationOneHeading}</h3>
                            <ul className="how-to-apply--pgt--js__options">
                                {locationOneDateSelectionButton}
                                {locationOneDateSelectionLink}
                            </ul>
                            <h3>{locationTwoHeading}</h3>
                            <ul className="how-to-apply--pgt--js__options">
                                {locationTwoDateSelectionButton}
                                {locationTwoDateSelectionLink}
                            </ul>
                        </React.Fragment>
                    )}
                </div>
            </>
        ),
        alternative = props.element.dataset.otheryear &&
            props.element.dataset.otherurl && (
                <p className="how-to-apply--pgt--js__modal__alternative">
                    <a href={props.element.dataset.otherurl}>
                        {props.element.dataset.otheryear} entry is also
                        available.
                    </a>
                </p>
            );
    // End modal wrapper render

    // Focus trap
    const [focusTrap, setFocusTrap] = useState({});

    let modalRef = null,
        modalContentRef = null;
    const getModal = () => modalRef,
        getModalContent = () => modalContentRef;

    useEffect(() => {
        if (!focusTrap.activate) {
            setFocusTrap(
                createFocusTrap(getModalContent(), {
                    initialFocus: getModalContent().querySelector(
                        '.how-to-apply--pgt--js__options button, .how-to-apply--pgt--js__options a'
                    ),
                    onDeactivate: () => setModalVisible(false),
                    clickOutsideDeactivates: true,
                })
            );
        }

        if (modalVisible) {
            focusTrap.activate && focusTrap.activate();
            disableBodyScroll(getModal());
        } else {
            focusTrap.deactivate && focusTrap.deactivate();
            enableBodyScroll(getModal());
        }
    }, [modalVisible]);
    // End focus trap

    // Render the 'Apply now' CTA which launches the modal
    return (
        <>
            <ul className="cta-block">
                <li className="cta-block__cta">
                    <button
                        className="primary-cta-arrow--bright"
                        onClick={() => {
                            setModalVisible(true);
                            setFirstStep(true);
                            setDateSelection();
                            setMethodSelection();
                            setBtnSelection();
                            setLinkSelection();
                            filterQualificationData(entryPoints);
                        }}
                    >
                        <span>Apply now</span>
                    </button>
                </li>
            </ul>
            <div
                className="how-to-apply--pgt--js__modal"
                data-open={modalVisible}
                ref={(modal) => (modalRef = modal)}
            >
                <div
                    className="how-to-apply--pgt--js__modal__content"
                    ref={(modalContent) => (modalContentRef = modalContent)}
                >
                    <button
                        className="how-to-apply--pgt--js__close"
                        onClick={() => {
                            focusTrap.deactivate && focusTrap.deactivate();
                            enableBodyScroll(getModal());
                            setModalVisible(false);
                        }}
                    >
                        <span
                            className="fas fa-times icon"
                            aria-label="Close"
                        ></span>
                    </button>

                    {/* Display 'Start again' reset if user is not in the modal's first selection screen */}
                    {!firstStep ? (
                        <div
                            className="how-to-apply--pgt--js__modal__reset"
                            style={{ display: 'block' }}
                        >
                            <button
                                onClick={() => {
                                    // Clear options display and render what user would see when modal is first opened
                                    setFirstStep(true);
                                    filterQualificationData(entryPoints);
                                }}
                            >
                                <span className="fas fa-redo"></span>{' '}
                                <span>Start again</span>
                            </button>
                        </div>
                    ) : null}
                    <ol className="how-to-apply--pgt--js__modal__progress">
                        {progressQualification}
                        {progressSubject}
                        {progressDate}
                        {progressMethod}
                        {furtherStepsPendingIndicator}
                    </ol>
                    <ol className="how-to-apply--pgt--js__modal__progress--mobile">
                        {progressQualification}
                        {progressSubject}
                        {progressDate}
                        {progressMethod}
                        {furtherStepsPendingIndicator}
                    </ol>
                    <div className="how-to-apply--pgt--js__modal__content-wrapper">
                        <h2>Start in {props.element.dataset.year}</h2>
                    </div>
                    {qualificationQuestion}
                    <div className="how-to-apply--pgt--js__modal__content-wrapper">
                        {alternative}
                    </div>
                </div>
            </div>
        </>
    );
}

HowToApply.propTypes = {
    config: PropTypes.array,
    element: PropTypes.object,
};

export default HowToApply;
